import * as React from 'react';
import { Col, Row } from 'reactstrap';
import CardView from '../../../content-parts/Card/CardView'

class Processes extends React.PureComponent {
    public render() {       

        return (
            <Row className="list-cards content-center" xs={1} sm={2} md={4}>
                <Col>
                    <CardView to="/processes/optimization" imgPath="img/iconImg/processOptimization.png" alt="Process Optimization" 
                                cardText="Optimize your processes by choosing the best combination of working temperature and the ideal cartridge." 
                                cardTitle="Process optimization"/>
                </Col>
                <Col>
                    <CardView to="/processes/assistant" imgPath="img/iconImg/solderingAssistant.png" alt="Soldering Assistant" 
                                cardText="Pre-stored soldering joint and compare in real-time with all the new soldering processes." 
                                cardTitle="Soldering assistant"/>
                </Col>
            </Row>
        );
    }
};

export default Processes;
