import * as React from 'react';
import { Col, Row } from 'reactstrap';
import CardView from '../../../content-parts/Card/CardView'

class Traceability extends React.PureComponent {
    public render() {

        return (
            <div className="list-cards">
                <Row xs={1} sm={2} md={4}>
                    <Col>
                        <CardView to="/traceability/task" imgPath="img/iconImg/tasksTraceability.png" alt="Tasks Traceability" 
                                    cardText="Access the list of registered jobs and create new tasks." 
                                    cardTitle="Tasks"/>
                    </Col>
                    <Col>
                        <CardView to="/traceability/workprocess" imgPath="img/iconImg/workProcess.png" alt="Work Process" 
                                    cardText="Register your work in the traceability system." 
                                    cardTitle="Work process"/>
                    </Col>
                    <Col>
                        <CardView to="/traceability/workbenchs" imgPath="img/iconImg/workBench.png" alt="Work Benchs" 
                                    cardText="Manage workbenchs and connected stations." 
                                    cardTitle="Workbenchs"/>
                    </Col>
                </Row>
            </div>
        );
    }
};

export default Traceability;
